import React from "react";
import "./WebPlayer.scss";
import { Link } from "react-router-dom";
const Webplayer = () => {
  const thumb1 = "./assets/img/changekelas/hidroponik/selada-hidroponik.png";
  const iPlayer = "./assets/icon/play-circle.svg";

  return (
    <section className="webplayer">
      <div className="container">
      <div className="webplayer-wrapper">
        {/* daftar-materi */}
        <div className="daftar-materi">
          <img src={thumb1} alt="gambar-thumbnil" className="gambar-thumbnil" />
          <div>
            <p className="categori-materi">Hidroponik</p>
            <h2 className="judul-materi">Kelas Budidaya Selada metode Hidroponik</h2>
            <p className="mentor-materi">Mentor : Tholib</p>
            <p className="judul-list">Daftar Materi</p>
            <div className="list">
              <Link to="/">
                <img src={iPlayer} alt="thumb" /> Materi I - Persiapan sebelum
                budidaya Selada
              </Link>
              <Link to="/">
                <img src={iPlayer} alt="thumb" /> Materi II - Memilih bibit yang
                benar
              </Link>
              <Link to="/">
                <img src={iPlayer} alt="thumb" /> Materi III - Cara menyemai
                agar hasil lebih maksimal
              </Link>
              <Link to="/">
                <img src={iPlayer} alt="thumb" /> Materi IV - Memulai proses
                penanaman Selada
              </Link>
              <Link to="/">
                <img src={iPlayer} alt="thumb" /> Materi V - Teknik pemupukan
                Selada yang tepat
              </Link>
              <Link to="/">
                <img src={iPlayer} alt="thumb" /> Materi VI - Perawatan Selada
                dan penanaman
              </Link>
            </div>     
          </div>
        </div>
        {/* materi */}
        <div className="materi">
          <div className="link-video">
            <iframe
              src="https://www.youtube.com/embed/t1IbDyd5F1g"
              title="YouTube video player"
              frameborder="0"
              allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
              allowfullscreen
            ></iframe>
          </div>
          <h3>Materi I - Persiapan sebelum budidaya Selada</h3>
          <div className="btn-webplayer">
          <button><i class="arrow left"></i> Materi Sebelumnya</button>
          <button>Materi Selanjutnya <i class="arrow right"></i></button>
          </div>
        </div>
      </div>
    </div>
    </section>
  );
};

export default Webplayer;
